.my-slider-gallery {
    position: relative;
    width: 100%;
}

.slides {
    position: relative;
    width: 100%;
    overflow: hidden;
}

.slide {
   
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.slide img {
    width: 100%;
    height: auto;
}

.slider-button-prev,
.slider-button-next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    z-index: 10;
}

.slider-button-prev {
    left: 10px;
}

.slider-button-next {
    right: 10px;
}